<template>
  <div class="back">
    <v-breadcrumbs :items="breadList" class="ml-12">
      <template v-slot:divider>
        <v-icon>mdi-chevron-right</v-icon>
      </template>
    </v-breadcrumbs>
    <v-card class="mx-auto mb-12" max-width="1000" min-height="2850">
      <v-form class="pl-8">
        <div class="pt-8"><span class="red--text">*</span>第一步：项目名称</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder="请输入项目名称，20个字以内"
            :rules="[v => !!v || '项目名称为必填项']"
            v-model="form.sci.entryname"
          ></v-text-field>
        </v-col>
        <div class="pt-8">
          <span class="red--text">*</span>第二步：成果负责人
        </div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder="请输入成果负责人姓名"
            v-model="form.sci.charge"
            :rules="[v => !!v || '成果负责人姓名为必填项']"
          ></v-text-field>
          <v-text-field
            required
            placeholder="请输入成果负责人职称信息"
            :rules="[v => !!v || '成果负责人职称信息为必填项']"
            v-model="form.sci.position"
          ></v-text-field>
        </v-col>
        <div class="mt-5">
          <span class="red--text">*</span>第三步：我的科研类型（可多选）
        </div>
        <v-row>
          <v-checkbox
            v-model="form.disciplines"
            class="ml-4 mx-2"
            value="机械"
            label="机械"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="电气"
            label="电气"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="能源动力"
            label="能源动力"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="电子信息"
            label="电子信息"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="新材料"
            label="新材料"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="化工"
            label="化工"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="生物医疗"
            label="生物医疗"
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value="航空航天"
            label="航空航天"
          ></v-checkbox>
        </v-row>
        <div class="mt-5">
          <span class="red--text pt-5">*</span>第四步：我的科研成果介绍
        </div>
        <v-col cols="12" sm="8">
          <v-textarea
            v-model="form.sci.results"
            filled
            placeholder="请输入科研成果简介，200字以内"
            :rules="[v => !!v || '项目名称为必填项']"
          ></v-textarea>
        </v-col>
        <div class="mt-5">
          <span class="red--text">*</span>第五步：技术创新点
        </div>
        <div style="height: 610px;">
          <quill-editor
            class="mr-8"
            max-height="400"
            v-model="form.sci.tecNew"
            ref="myQuillEditor"
            style="height: 500px;"
            :options="editorOption"
          ></quill-editor>
        </div>
        <div class="mt-5">第六步：市场前景及应用</div>
        <div style="height: 610px;">
          <quill-editor
            class="mr-8"
            max-height="400"
            v-model="form.sci.future"
            ref="myQuillEditor"
            style="height: 500px;"
            :options="editorOption"
          ></quill-editor>
        </div>
        <div class="mt-5">第七步：技术成熟度</div>
        <v-radio-group v-model="form.sci.maturity" row>
          <v-radio class="ml-4" value="概念验证" label="概念验证"></v-radio>
          <v-radio class="ml-4" value="原理样机" label="原理样机"></v-radio>
          <v-radio class="ml-4" value="工程样机" label="工程样机"></v-radio>
          <v-radio class="ml-4" value="中试" label="中试"></v-radio>
          <v-radio class="ml-4" value="产业化" label="产业化"></v-radio>
        </v-radio-group>
        <div class="mt-5"><span class="red--text">*</span>第八步：联系方式</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            filled
            label="项目联系人姓名"
            placeholder="请输入联系人姓名"
            :rules="[v => !!v || '联系人姓名为必填项']"
            v-model="form.sci.contacts"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="项目联系人电话"
            placeholder="请输入联系电话"
            :rules="[v => !!v || '联系人电话为必填项']"
            v-model="form.sci.tel"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="项目联系邮箱"
            placeholder="请输入联系邮箱"
            :rules="[v => !!v || '联系人邮箱为必填项']"
            v-model="form.sci.mail"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="个人信息"
            placeholder="请输入个人联系方式"
            :rules="[v => !!v || '个人信息为必填项']"
            v-model="form.sci.information"
          ></v-text-field>
        </v-col>
        <div class="mt-5">第八步：科研论文</div>
        <v-file-input
          v-model="file"
          accept="application/pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/msword,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
          placeholder="只能上传pdf/docx/excel文件"
          label="科研论文上传"
          prepend-icon="mdi-paperclip"
        >
        </v-file-input>
        <v-row class="mt-12 mb-12">
          <div class="mr-12 ml-3">
            <v-btn
              width="120px"
              depressed
              large
              color="info"
              @click="releaseSci()"
              :loading="loading"
              :disabled="loading"
              >发布成果</v-btn
            >
          </div>
          <div class="mr-12">
            <v-btn
              width="120px"
              class="white--text"
              depressed
              large
              color="#AAAAAA"
              @click="cancel()"
              >取消</v-btn
            >
          </div>
        </v-row>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

export default {
  name: 'ReleaseScience',
  components: {
    quillEditor
  },
  data() {
    return {
      loading: false,
      file: null,
      editorOption: {
        theme: 'snow', // or 'bubble'
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'][({ header: 1 }, { header: 2 })],
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ script: 'sub' }, { script: 'super' }],
            [{ indent: '-1' }, { indent: '+1' }],
            [{ size: ['small', false, 'large', 'huge'] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ color: [] }],
            [{ align: [] }],
            ['link', 'image'],
            ['sourceEditor']
          ]
        }
      },
      form: {
        sci: {
          entryname: '',
          tel: '',
          mail: '',
          position: '',
          information: '',
          charge: '',
          results: '',
          maturity: '',
          contacts: '',
          tecNew: null,
          future: null
        },
        File: null,
        disciplines: []
      },
      breadList: [
        {
          text: '您的位置：首页',
          disabled: false,
          href: '/university'
        },
        {
          text: '发布科研成果',
          disabled: true
        }
      ]
    }
  },
  methods: {
    clearInput() {
      this.form = {}
    },
    async releaseSci() {
      let formData = new FormData()
      formData.append('File', this.file)
      let resFile = await this.$http.post('/api/school/upload', formData, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
      })
      if (resFile.data.code === 200) {
        this.form.File = resFile.data.data
      }
      let res = await this.$http.post('/api/school/add', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('发布科研成果成功！')
        this.clearInput()
        this.$router.push('/university/history')
      } else {
        this.clearInput()
        this.$message.error('发布科研成果失败！')
      }
      this.clearInput()
    },
    cancel() {
      this.clearInput()
      this.$router.push('/university')
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
